import clsx from 'clsx'
import Product from 'components/Product/Product'
import s from './Products.module.scss'
import p1 from 'assets/images/products/p-1.png'
import p2 from 'assets/images/products/p-2.png'
import p3 from 'assets/images/products/p-3.png'
import p4 from 'assets/images/products/p-4.png'

const Products = () => {
  const products = [
    {
      name: 'Airpods 4 PRO',
      availability: 'available',
      imgRef: p1,
      colors: ['red', 'yellow', 'blue'],
      stars: 3,
      reviews: 24,
      price: '299$'
    }
  ]

  const renderedProducts = products.map(p => (
    <Product
      name={p.name}
      availability={p.availability}
      imgRef={p.imgRef}
      colors={p.colors}
      stars={p.stars}
      reviews={p.reviews}
      price={p.price}
    />
  ))

  return (
    <div className={clsx(s['Products'])}>
      
      <div className={clsx(s['title'])}>
        Products
      </div>

      <ul className={clsx(s['list'])}>
        {renderedProducts}
      </ul>

    </div>
  )
}

export default Products
