import clsx from 'clsx'
import s from './Grid.module.scss'
import manImg from 'assets/images/image-30.png'
import airpods_1 from 'assets/images/image-28.png'
import airpods_2 from 'assets/images/image-27.png'
import airpods_3 from 'assets/images/image-29.png'
import airpods_4 from 'assets/images/image-31.png'

const Grid = () => {
  return (
    <div className={clsx(s['Grid'])}>

      <div className={clsx(s['g1'])}>
        <img
          src={manImg}
        />
      </div>

      <div className={clsx(s['g2'])}>
        <div className={clsx(s['title'])}>
          Simplicity
        </div>
        <div className={clsx(s['text'])}>
          Lorem ipsum dolor sit amet, consectetur
          adipiscing elit. Cursus lacus, senectus
          tincidunt semper ultricies dignissim nunc.
          Sit vitae
        </div>
      </div>

      <div className={clsx(s['g3'])}>
        <img
          src={airpods_1}
        />
      </div>

      <div className={clsx(s['g4'])}>
        <img
          src={airpods_2}
        />
      </div>

      <div className={clsx(s['g5'])}>
        <div className={clsx(s['title'])}>
          Availability
        </div>
        <div className={clsx(s['text'])}>
          Lorem ipsum dolor sit amet, consectetur
          adipiscing elit. Cursus lacus, senectus
          tincidunt semper ultricies dignissim
          nunc. Sit vitae.
        </div>
      </div>

      <div className={clsx(s['g6'])}>
        <img
          src={airpods_3}
        />
      </div>

      <div className={clsx(s['g7'])}>
        <div className={clsx(s['title'])}>
          Minimalism
        </div>
        <div className={clsx(s['text'])}>
          Lorem ipsum dolor sit amet, consectetur
          adipiscing elit. Cursus lacus, senectus
          tincidunt semper ultricies dignissim nunc.
          Sit vitae
        </div>
      </div>

      <div className={clsx(s['g8'])}>
        <img
          src={airpods_4}
        />
      </div>

    </div>
  )
}

export default Grid
