import clsx from 'clsx'
import s from './AirPods.module.scss'
import airpodsWhite from 'assets/images/airpods-white.png'
import airpodsBlack from 'assets/images/airpods-black.png'

const AirPods = () => {
  return (
    <div className={clsx(s['AirPods'])}>
      
      <div className={clsx(s['block'])}>
        <div className={clsx(s['img-container'])}>
          <img
            src={airpodsWhite}
            className={clsx(s['img'])}
          />
        </div>
        <div className={clsx(s['body'])}>
          <div className={clsx(s['title'])}>
            AirPods Pro 4 White
          </div>
          <div className={clsx(s['about-text'])}>
            {`Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vulputate
              nisl in sed feugiat libero, arcu, sed. Lobortis mi vel dui interdum
              porttitor vel bi bendum convallis tincidunt. Pellentesque et ac ul
              trices orci fames. Mi tristique volutpat molestie vestibulum sagit
              tis. Varius sed molestie augue q uam odio. Nulla interdum porttito
              r vel bibendum convallis tincidun`}
          </div>
          <div className={clsx(s['contact-btn'])}>
            Contact
          </div>
        </div>
      </div>

      <div className={clsx(s['block'])}>

        <div className={clsx(s['body'])}>
          <div className={clsx(s['title'])}>
            AirPods Pro 4 White
          </div>
          <div className={clsx(s['about-text'])}>
            {`Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vulputate
              nisl in sed feugiat libero, arcu, sed. Lobortis mi vel dui interdum
              porttitor vel bi bendum convallis tincidunt. Pellentesque et ac ul
              trices orci fames. Mi tristique volutpat molestie vestibulum sagit
              tis. Varius sed molestie augue q uam odio. Nulla interdum porttito
              r vel bibendum convallis tincidun`}
          </div>
          <div className={clsx(s['contact-btn'])}>
            Contact
          </div>
        </div>

        <div className={clsx(s['img-container'])}>
          <img
            src={airpodsBlack}
            className={clsx(s['img'])}
          />
        </div>

      </div>

    </div>
  )
}

export default AirPods
