import clsx from 'clsx'
import s from './Intro.module.scss'
import airpodsImg from 'assets/images/airpods-1.png'

const Intro = () => {
  return (
    <div className={clsx(s['Intro'])}>
      
      <div className={clsx(s['left'])}>

        <div className={clsx(s['title'])}>
          {`Feel the purity of melodies
          with AirPods`}
        </div>

        <div className={clsx(s['about-text'])}>
          {`Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris
          sollicitudin cras aliquam vitae commodoconsectetur nulla diam enim.
          commodo consectetur nulla diam`}
        </div>

        <div className={clsx(s['buttons'])}>
          <button className={clsx(s['contact-btn'])}>
            Contact
          </button>
          <div className={clsx(s['location'])}>
            <span className={clsx(['material-icons', s['location-icon']])}>
              location_on
            </span>
            Location
          </div>
        </div>

      </div>

      <div className={clsx(s['right'])}>
        <img
          src={airpodsImg}
        />
      </div>

    </div>
  )
}

export default Intro
