import clsx from 'clsx'
import s from './Product.module.scss'
import pt from 'prop-types'
import starGrey from 'assets/icons/star-grey.png'
import starYellow from 'assets/icons/star-yellow.png'

const Product = (props) => {
  let availability = null

  switch (props.availability) {
    case 'available':
      availability = (
        <div className={clsx(s['availability'], s['available'])}>
          Available now
        </div>
      )
      break;
    case 'not-available':
      availability = (
        <li className={clsx(s['availability', s['not-available']])}>
          Not available
        </li>
      )
      break;
    case 'in-delivery':
      availability = (
        <li className={clsx(s['availability', s['delivery']])}>
          In delivery
        </li>
      )
      break;
  }

  return (
    <li className={clsx(s['Product'])}>

      <div className={clsx(s['header'])}>

        <div className={clsx(s['name'])}>
          {props.name}
        </div>

        {availability}

      </div>

      <div className={clsx(s['img-container'])}>
        <img src={props.imgRef} />
      </div>

      <div className={clsx(s['colors'])}>
        <div className={clsx()}>
          Available colors
        </div>
        <ul className={clsx(s['colors-list'])}>
          {props.colors.map(c => (
            <li
              className={clsx(s['colors-item'])}
              style={{ backgroundColor: c }}
            ></li>
          ))}
        </ul>
      </div>

      <div className={clsx(s['reviews'])}>

        <ul className={clsx(s['stars'])}>
          <img src={starYellow} />
          <img src={starYellow} />
          <img src={starYellow} />
          <img src={starGrey} />
          <img src={starGrey} />
        </ul>

        <div className={clsx()}>
          (28 Reviews)
        </div>

      </div>

      <div className={clsx(s['price'])}>
        {props.price}
      </div>

    </li>
  )
}

Product.propTypes = {
  name: pt.string.isRequired,
  availability: pt.string.isRequired,
  imgRef: pt.string.isRequired,
  colors: pt.array.isRequired,
  price: pt.string.isRequired
}

export default Product
